// 处理content，让excel数据替换placeholder
function dealContentPlaceholder(content, excelData, phoneNum) {
    let isExcelData = false;
    let isPlaceHolder = false;
    if (excelData === []) {
        return [content, isExcelData, isPlaceHolder];
    } else {
        for (let i = 0; i < excelData.length; i++) {
            try {
                let whatsAppNumber = excelData[i]['whatsAppNumber'] ? excelData[i]['whatsAppNumber'] : '';
                if (whatsAppNumber === phoneNum) {
                    isExcelData = true;
                    let firstName = excelData[i]['firstName'] ? excelData[i]['firstName'] : '';
                    let lastName = excelData[i]['lastName'] ? excelData[i]['lastName'] : '';
                    let other = excelData[i]['other'] ? excelData[i]['other'] : '';
                    if (content.indexOf('{{WhatsApp Number}}') !== -1 ||
                        content.indexOf('{{First Name}}') !== -1 ||
                        content.indexOf('{{Last Name}}') !== -1 ||
                        content.indexOf('{{Other}}') !== -1) {
                        isPlaceHolder = true
                    }
                    content = content.replaceAll('{{WhatsApp Number}}', whatsAppNumber);
                    content = content.replaceAll('{{First Name}}', firstName);
                    content = content.replaceAll('{{Last Name}}', lastName);
                    content = content.replaceAll('{{Other}}', other);
                }
            } catch (e) {
                console.log(e);
            }
        }
        return [content, isExcelData, isPlaceHolder];
    }
}

