function insertIframe() {
    chrome.storage.local.get(['firstInstall'], function (res) {
        if (res.firstInstall) {
            chrome.storage.local.set({firstInstall: false});
            let iFrame = document.createElement("iframe");
            iFrame.src = chrome.runtime.getURL("li-popup/li-popup.html");
            iFrame.width = '430px';
            iFrame.height = '470px';
            iFrame.style.zIndex = '400';
            iFrame.style.position = 'absolute';
            iFrame.style.right = '0';

            let mantle = document.createElement('div');
            mantle.className = 'mantle'
            mantle.style.backgroundColor = 'rgba(0, 0, 0, 0.5)';
            mantle.style.zIndex = '200';
            mantle.style.height = '100%';
            mantle.style.width = '100%';
            mantle.style.position = 'absolute';

            document.body.insertBefore(iFrame, document.body.firstChild);
            document.body.insertBefore(mantle, document.body.firstChild);

            document.addEventListener('click', handlerClick)  // 监听 document 点击事件
        }
    })

}

function handlerClick() {
    let isSelf = document.getElementsByTagName('iFrame')[0].contains(event.target)  // 这个是自己的区域
    if (!isSelf) {
        document.getElementsByTagName('iFrame')[0].remove()
        document.getElementsByClassName('mantle')[0].remove()
        document.removeEventListener('click', handlerClick)
    }
}
